.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #3d5bda;
  border-color: #3d5bda #3d5bda rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin: 50px 120px 0;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  box-sizing: border-box;
  width: 40px;
  height: 40px;
  transform-origin: center;
  border: 3px solid rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) #ff3d00 #ff3d00;
  border-radius: 50%;
  margin: auto;
  animation: rotationBack .5s linear infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.loader:before {
  width: 32px;
  height: 32px;
  border-color: #3d5bda #3d5bda rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  animation: rotation 1.5s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

